                   
'use strict';                                              
import 'regenerator-runtime';
import _ from 'underscore';
import T from '/bext/pub/locale.js';
import be_vpn_util from '/bext/vpn/pub/util.js';
const E = {};
const e = _.escape;
const t = v=>e(T(v));

E.country_list_item = opt=>{
    let flag_class = opt.disable&&'flag_disable' || opt.mitm&&'flag_unblock' ||
        opt.protect&&'flag_protect' || opt.country.toLowerCase();
    return `
<li class="country" data-country="${opt.country}"
  data-mitm="${!!opt.mitm}" data-disable="${!!opt.disable}"
  data-premium="${opt.type=='premium'? 1 : 0}" data-protect="${!!opt.protect}">
  <a class="f32 ui_lock_parent">
  <span class="ui_lock_container flag ${flag_class}"></span>
  <span class="flag_name" title="${opt.name}"> ${opt.name}</span>
  ${opt.type=='premium' ?
  `<span class="flag_type">${t('PLUS')}</span>` : ''}
  </a>
</li>`;
};

E.menu_products = opt=>{
    return `
<div class=products>
  <a href="https://hola.org/unblock/popular/${opt.country}"
    target=_blank class=more-sites>
    ${t('Unblock more sites...')}</a>
  <div class="list">
    <a href="https://hola.org/plus_setup?platform=windows&utm_source=holaext&utm_content=${opt.origin}-products"
      target=_blank title="Windows" class="item windows"></a>
    <a href="https://hola.org/plus_setup?platform=mac&utm_source=holaext&utm_content=${opt.origin}-products"
      target=_blank title="macOS" class="item mac"></a>
    <a href="https://play.google.com/store/apps/details?id=org.hola.prem&referrer=utm_source%3Dholaext%26utm_content%3D${opt.origin}-products"
      target=_blank title="Android" class="item android"></a>
    <a href="https://itunes.apple.com/us/app/hola-privacy-vpn-app-browser/id903869356?mt=8&ct=holaext-${opt.origin}-products"
      target=_blank title="iOS" class="item ios"></a>
    <a href="https://hola.org/plus_setup?platform=smarttv&utm_source=holaext&utm_content=${opt.origin}-products"
      target=_blank title="Smart TV" class="item smarttv"></a>
    <a href="https://hola.org/plus_setup?platform=console&utm_source=holaext&utm_content=${opt.origin}-products"
      target=_blank title="${t('Consoles')}" class="item console"></a>
  </div>
</div>`;
};


E.menu_account = opt=>{
    let plus_upgrade = be_vpn_util.plus_ref('holaext-menu-account-upgrade',
        {utm_source: 'holaext', utm_content: 'menu-account-upgrade'});
    let plus_try = be_vpn_util.plus_ref('holaext-menu-account-try',
        {utm_source: 'holaext', utm_content: 'menu-account-try'});
    return `
<div class=header>${t('My account')}</div>
  <div class=info>
    <div class=title>${t('Account type:')}</div>
    <div class=value>
      ${opt.is_premium ? `${t('PLUS')}` : `${t('Free')}`}
      ${!opt.is_premium ? `
      <a href="${plus_upgrade}"
        class=upgrade target=_blank>${t('Upgrade')}</a>` : `
      <a class=manage href="https://hola.org/cp?utm_source=holaext&utm_content=menu-account-manage"
        target=_blank>${t('Manage')}</a>`}
    </div>
    ${!opt.is_tpopup ? `
    <div class=title>${t('User:')}</div>
    <div class="value ellipsis">${e(opt.email)}</div>` : ''}
  </div>
  ${opt.is_premium ? `${opt.products}` : ''}
  <div class=log-out>
    <button class=menu-button>${t('Log out')}</button>
  </div>
  ${!opt.is_premium ? `
  <div class=try-premium>
    <a href="${plus_try}" target=_blank>${t('Try Hola VPN PLUS')}</a>
  </div>` : ''}`;
};

E.menu_button = ()=>{
    return `
<i class="popup-header-controls-button popup-header-nav"
  title="${t('Menu')}">
  <svg class="hamburger-top" height="100%" width="100%"
    viewBox="0 0 14 2" style="display:none;">
    <path d="M1 2h12c.6 0 1-.4 1-1s-.4-1-1-1H1C.4 0 0 .4 0 1s.4 1 1 1z"/>
  </svg>
  <svg class="hamburger-middle" height="100%" width="100%"
    viewBox="0 0 14 2" style="display:none;">
    <path d="M1 2h12c.6 0 1-.4 1-1s-.4-1-1-1H1C.4 0 0 .4 0 1s.4 1 1 1z"/>
  </svg>
  <svg class="hamburger-bottom" height="100%" width="100%"
    viewBox="0 0 14 2" style="display:none;">
    <path d="M1 2h12c.6 0 1-.4 1-1s-.4-1-1-1H1C.4 0 0 .4 0 1s.4 1 1 1z"/>
  </svg>
</i>`;
};

E.menu = opt=>{
    let plus_try = be_vpn_util.plus_ref('holaext-menu-try',
        {utm_source: 'holaext', utm_content: 'menu-try'});
    return `
<div class=modal-menu>
  <div
    class="modal-menu-general${opt.display_name ? ' user-signed' : ''}">
    <div class="user-info-wrapper">
      ${opt.display_name ? `
      <div class="user-info">
        <div class=user-status>
          ${opt.is_premium ? `${t('Hola VPN PLUS')}` :
          `${t('Hola Free VPN')}`}
        </div>
        <div class=user-name>
          ${opt.is_tpopup ? `${t('Your account')}` :
          `${e(opt.display_name)}`}
        </div>
      </div>` : `
      <a class="user_link user_link_login menu-button" id=sign_in target=_blank
        href="https://hola.org/signin?utm_source=holaext&utm_content=menu">
        ${t('Log in')}
      </a>`}
    </div>
    <ul class="menu-items">
      ${opt.is_premium ? `
      <li class="menu-item-protect menu-item">
        <a href="https://hola.org/plus_setup?platform=windows&utm_source=holaext&utm_content=menu-protect"
          target=_blank>
          ${t('Protect entire PC')}
        </a>
      </li>
      <li class="menu-item-install menu-item">
        <a href="https://hola.org/plus_setup?utm_source=holaext&utm_content=menu-install"
          target=_blank>
          ${t('Install on other devices')}
        </a>
      </li>
      <li class="menu-item-divider menu-item"></li>` : ''}
      ${opt.is_lang ? `
      <li class="l_menuitem_lang menu-item-lang menu-item">
        <a href="#">${t('Language')}</a>
      </li>` : ''}
      <li class="menu-item menu-item-settings">
        <a>${t('Settings')}</a>
      </li>
      <li class="menu-item menu-item-hide-ip">
        <a href=#>${t('Hide my IP')}</a>
      </li>
      <li class="menu-item-help menu-item"><a
        class="user_link"
        href="https://hola.org/faq"
        target=_blank>${t('Help')}</a></li>
      <li class="menu-item-about menu-item">
        <a>${t('About')}</a>
      </li>
      ${opt.dev_mode ? `
      <li class="menu-item-debug menu-item">
        <a>${t('Debug')}</a>
      </li>` : ''}
      ${!opt.is_premium ? `
      <li class="menu-item-premium menu-item">
        <a href="${plus_try}" target=_blank>
          ${t('Try Hola VPN PLUS')}
        </a>
      </li>` : ''}
      <li class="menu-item-issue menu-item">
        <a>${t('Report a problem')}</a>
      </li>
    </ul>
    ${opt.products}
  </div>
</div>`;
};

E.switch_privacy = opt=>{
    return `
<label>
  <input type=checkbox>${t('Total privacy - make me anonymous')}
</label>`;
};

E.modal_view = opt=>{
    return `
<div>
  <div class=btn_close></div>
  ${opt.signin ? `
  <a target=_blank class=sign-in
    href="https://hola.org/signin?utm_source=holaext&utm_content=${opt.signin}">
    ${t('Log in')}</a>` : ''}
  <div class=message>${opt.text_main}</div>
  <div class=buttons><div class=btn_no>${opt.text_no}</div>
  <div class=btn_yes>${opt.text_yes}</div>
</div>`;
};

E.popup_disabled = opt=>{
    return `
<h1>${t('Hola is off')}</h1>
<i class=${opt.class_name}-icon></i>
<h2>${t('Click to turn it on')}</h2>`;
};

E.rated = opt=>{
    return `
<div>
  ${opt.browser=='chrome' && opt.rating==5 ? `
  <a class=rate-us target=_blank
    href="https://chrome.google.com/webstore/detail/hola-better-internet/gkojfkhlekighikafcpjkiklfbnlmeio/reviews">
    ${t('Rate us in webstore')}</a>` : ''}
  ${!opt.is_premium ? `
  <button class="popup-button popup-button-try">
    ${t('Try Hola VPN PLUS')}
  </button>` : ''}
</div>`;
};


E.site_premium = opt=>{
    return `
<div class=top>
  <div class=image></div>
  <div class=titles>
    <div class=title>${opt.root_url}</div>
    <div class=subtitle translate>Requires specialized servers</div>
  </div>
  <div class=try>${t('Subscribe to PLUS')}</div>
</div>
<div class=bottom></div>`;
};

export default E;
